<template>
	<v-container fluid grid-list-xl fill-height>
     <v-layout justify-center  align-center>
			<v-flex xs12>
				<material-card  color="green">
					<v-card-text>
						<v-layout row>
							<v-flex sm6>
              				
							</v-flex>
							<v-flex sm6>
								<v-text-field
									v-model="keyword"
									append-icon="mdi-magnify"
									label="Search"
									single-line
									hide-details
								/>
							</v-flex>
						</v-layout>
						<v-layout row wrap>
							<v-flex sm12>
								<v-data-table :headers="headers" :items="items" class="elevation-1"
								:hide-actions="items.length == 0"
								>
									<template slot="headerCell" slot-scope="{ header }">
										<span class="subheading font-weight-light text-success text--darken-3 cn">
											{{ header.text }}
										</span>
									</template>
									<template slot="items" slot-scope="{ item }">
										<td>{{ item.name }}</td>
										<td>{{ item.email }}</td>
										<td>{{ item.phone }}</td>
										<td class="text-xs-right">{{ item.salary }}</td>
									</template>
								</v-data-table>
							</v-flex>
						</v-layout>
						<v-layout v-if="!responsive" v-show="items.length > 0" row>
							<v-flex sm12>
              				<div class="text-xs-center">
									<span class="mr-3">
										1-5 of 10
									</span>
									  
									<v-pagination
										v-model="page"
										:length="15"
										:total-visible="7"
									/>
								</div>
							</v-flex>
						</v-layout>	
					</v-card-text>
				</material-card>
			</v-flex>
     </v-layout>
	</v-container>
    
	
</template>

<script>
import { mapState } from 'vuex';
import MaterialCard from '../components/material/Card';

export default {
	name: 'UserView',
	components: {
		'material-card' : MaterialCard
	},
	data () {
		return {
			page: 1,
			keyword: '',
			headers: [
				{
					sortable: false,
					text: '姓名',
					value: 'name'
				},
				{
					sortable: false,
					text: 'Email',
					value: 'email'
				},
				{
					sortable: false,
					text: '手機',
					value: 'phone'
				}
    		]
		}
	},
	computed: {
      ...mapState({
         responsive: state => state.app.responsive
		}),
		items(){
			return [
				{
					name: 'stephen',
					email: 'stephen@gmail.com',
					phone: '0987654'
				}
			];
		}
   },
	watch: {
      'page' (val) {
         alert(val);
      }
   },
	mounted(){
		
	},
	methods: {
		
	}
}
</script>


